function sum(a, b) {
  return a + b;
}

// named export
// ter vrios exports dentro de um mesmo arquivo
// s pode chamar com o mesmo nome
// import precisa das chaves
export function sub(a, b){
  return a - b;
}

function mult(a, b) {
  return a * b;
}

function div(a, b){
  return a / b;
}
// Exportando tudo, porm deveria remover os outros exports 
// export * as utils;
const PI = 3.14;
export { mult as multiplicacao, div, PI };

// mtodo principal
// s pode ter um default por arquivo
// importar com qualquer nome
// no precisa utilizar chaves
export default sum;



